<template>
  <div id="chat-area" v-if="chatTabs==0" v-chat-scroll="{always: true, smooth: true}">
  
    <div id="chat-panel">
      <div class="slimScrollDiv" :style="'position: relative;  width: auto;'">
        <div class="chat-content" :style="'overflow-x: auto; width: auto;' + 'height:' +(windowHeight - 261)+'px'">
          >
          <div id="chat-content">
  
            <div class="ovh chat-item" v-for="(messageItem, index) in chatMsgData" :key="index">
              <div class="chat-msg">
                <img src="@/assets/gst_small_logo.png" data-name style="opacity:0.90">
                <div style="position: relative; margin-left: 35px; padding: 0px;" title>
                  <span class="name" data-name="0000010100...">{{ messageItem.sender | subStrAddress}}</span>
  
                  <!-- {{this.tronAddressNHex.substr(0,5)+'....'+this.tronAddressNHex.substr(this.tronAddressNHex.length-5,this.tronAddressNHex.length) -->
                  <span style="margin-right:10px">:</span>
                  <span class="message" title style="overflow-wrap: break-word; display: inline-block; max-width: 100%;">{{ messageItem.message}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div id="chat-send">
  
      <textarea id="chatTextarea" maxlength="150" v-on:keyup.enter="clickButton()" v-model="message" placeholder="Type your message…" title></textarea>
      <a @click="clickButton()" style="cursor:pointer;" class="send-btn">
        <img src="@/assets/images/icon/default/send.png" style="margin-top:25%" width="24px">
      </a>
    </div>
  </div>
</template>

<script>
  import {
    mapState
  } from "vuex";
  export default {
    name: "chat",
    sockets: {
      chatmessage: function(data) {
        // console.log(data);
        this.chatMsgData.push(data);
        console.log('asdasdas', this.chatMsgData);
      }
    },
  
    data() {
      return {
        windowHeight: 0,
        windowWidth: 0,
        message: "",
        chatMsgData: [],
        address: "guest"
      };
    },
    computed: {
      ...mapState(["chatTabs", "tronWeb", "tronLoggedIn", "tronAddress"]),
      checkTronLogin() {
        return this.tronLoggedIn;
      },
      checkTronAddress() {
        return this.tronAddress;
      }
    },
    created() {
     this.scroll();
      this.getChatlist();
      window.addEventListener("resize", this.handleResize);
      this.handleResize();

    },
    watch: {
      async checkTronLogin(value) {
        if (value) {
          const nonHex = this.tronWeb.address.fromHex(
            (await this.tronWeb.trx.getAccount()).address.toString()
          );
  
          this.address =
            nonHex.substr(0, 5) +
            "...." +
            nonHex.substr(nonHex.length - 5, nonHex.length);
  
          console.log(" Chat : GGWPlogin: " + this.address);
        }
      },
      async checkTronAddress(value) {
        console.log("Tron address :: " + value);
        if (!value == "none") {
          const nonHex = this.tronWeb.address.fromHex(value);
  
          // console.log("New address is this one : " + nonHex);
  
          this.address =
            nonHex.substr(0, 5) +
            "...." +
            nonHex.substr(nonHex.length - 5, nonHex.length);
  
          console.log(" Chat : GGWPaddr: " + this.address);
        }
      }
    },
    async mounted() {
      await this.$store.dispatch("getTronWebData");
  
      this.$store.dispatch("getLogin");
  
      this.$store.dispatch("getTronAddress");
  
      var addressCheck = "";
  
      if (await this.tronWeb.trx) {
        console.log("Chat: " + "true");
        console.log("Chat: " + (await this.tronWeb.trx.getAccount()).address);
        addressCheck = (await this.tronWeb.trx.getAccount()).address;
      } else {
        console.log("My bets: " + "False");
      }
  
      if (!addressCheck == "") {
        console.log("Chat : Address is not empty");
  
        const nonHex = this.tronWeb.address.fromHex(
          (await this.tronWeb.trx.getAccount()).address.toString()
        );
  
        this.address =
          nonHex.substr(0, 5) +
          "...." +
          nonHex.substr(nonHex.length - 5, nonHex.length);
      }
  
  
    },
  
    methods: {
      loginNow() {
        this.$store.dispatch("loginPopUpStatus", {
          isLogin: true
        });
      },
      clickButton: function() {
     
  
        if (this.message != '') {
          if (this.address == "guest") {
            this.$store.dispatch("loginPopUpStatus", {
              isLogin: true
            });
          }
            else {
              this.$socket.emit("chatmessage", {
                message: this.message,
                _id: 'roshan'
              });
               this.message = "";
            }
  
          } else {
            return false;
          }
      
        },
        handleResize() {
  
            this.windowHeight = window.innerHeight;
            this.windowWidth = window.innerWidth;
            if (this.windowWidth > 991) {
              this.$root.chatBoxShow = true;
            } else {
              this.$root.chatBoxShow = false;
            }
          },
          getChatlist: function() {
            this.$store.dispatch('getChatlist').then(data => {
              this.chatMsgData = data.data;
            });
          },
          scroll() {
  document.getElementById('chat-content').scrollTop = document.getElementById('chat-content').scrollHeight;
}
      },
      filters: {
        subStrAddress: function(address) {
          return address.substr(0, 5) + '...' + address.substr(address.length - 5, address.length);;
        }
      },
    };
</script>

