<template>
  <section id="chat-rank" class v-if="chatTabs==1">
    <div id="chatRankMenu">
      <table>
        <tr>
          <th style="width: 60px;"></th>
          <th style="text-align: center;" title>Player</th>
          <th title style="text-align: right; padding-right: 30px;">Wagered</th>
        </tr>
      </table>
    </div>
    <div
      class="slimScrollDiv"
      :style="'position: relative; overflow: auto; width: auto;' +'height:' +(windowHeight)+'px'"
    >
      <div id="chat-rank-sc" title style="height: 772px; overflow: hidden; width: auto;">
        <table>
          <tr v-for="(leaderBoardItem, index) in leaderboardData" :key="index">
            <td style="width: 60px;" v-if="index>2">
              <span>{{ index + 1 }}</span>
            </td>
            <td style="width: 60px;" title v-if="index==0">
              <img src="@/assets/images/rank/1.png" width="30" title>
            </td>
            <td style="width: 60px;" title v-if="index==1">
              <img src="@/assets/images/rank/2.png" width="30" title>
            </td>
            <td style="width: 60px;" title v-if="index==2">
              <img src="@/assets/images/rank/3.png" width="30" title>
            </td>
            <td class="chat-lv4" style="text-align: left;">
              <span style="font-size: 12px;"></span>

              {{ leaderBoardItem._id.substr(0,5)+'....'+leaderBoardItem._id.substr(leaderBoardItem._id.length-5 ,leaderBoardItem._id.length) }}
            </td>
            <td
              class="online"
              style="text-align: right; padding-right: 30px;"
            >{{ leaderBoardItem.amount + ' TRX' }}</td>
          </tr>
        </table>
      </div>
      <div
        class="slimScrollBar"
        style="background: rgb(255, 255, 255); width: 7px; position: absolute; top: 0px; opacity: 0.4; display: none; border-radius: 7px; z-index: 99; right: 1px; height: 772px;"
      ></div>
      <div
        class="slimScrollRail"
        style="width: 7px; height: 100%; position: absolute; top: 0px; display: none; border-radius: 7px; background: rgb(51, 51, 51); opacity: 0.2; z-index: 90; right: 1px;"
      ></div>
    </div>
  </section>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "rank",
  data() {
    return {
      windowHeight: 0,
      windowWidth: 0
    };
  },
  computed: {
    ...mapState(["chatTabs", "leaderboardData"])
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
    this.$store.dispatch("getLeaderBoard");
  },
  methods: {
    handleResize() {
      this.windowHeight = window.innerHeight;
      this.windowWidth = window.innerWidth;
      if (this.windowWidth > 991) {
        this.$root.chatBoxShow = true;
      } else {
        this.$root.chatBoxShow = false;
      }
    }
  }
};
</script>

