import Vue from "vue";
import Router from "vue-router";
import DicePage from "./components/dapps/dice/main.vue";
import DiceReferral from './components/dapps/dice/mainref.vue';
import CrownPage from './components/dapps/crown/main.vue';

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "home",
      component: DicePage
    },

    {
      path: "/dice",
      name: "dicePage",
      component: DicePage
    },
    {
      path: "/crown",
      name: "crownPage",
      component: CrownPage
    },

    {
      path: "/referral/:id",
      name: "referralPage",
      component: DiceReferral
    }
  ]
});
