<template>
  <tbody :class="betTabs==0?'':'hide'" v-if="this.loggedIn">
    <tr class="win" v-for="(item, index) in allMyBets" :key="index">
      <td></td>
      <td
        class="list-th"
        title
      >{{ item.player.substr(0,5)+'....'+item.player.substr(item.player.length-5 ,item.player.length) }}</td>

      <td class="list-th" title>
        <span class>{{ item.predictionStatus==true?'Over':'Under'}}</span>
        &nbsp;{{ item.prediction}}
      </td>
      <td
        :class=" item.predictionStatus=='0'?item.result>item.prediction?'losec':'winc':item.result<item.prediction?'losec':'winc'"
        title
      >{{ item.result}}</td>
      <td
        class="list-th"
        style="text-align: right;"
        title
      >{{ item.bets}}{{ item.coinType==0?'TRX':'GST'}}</td>
      <td class="list-th winc" style="text-align: right;">{{ item.payout==0?'----':item.payout}}</td>
      <td class="adj-th" style="width: 100px;"></td>
    </tr>
  </tbody>
  <tbody v-else :class="betTabs==0?'':'hide'">
    <tr>
      <td colspan="6">
        <p style="margin-top:20px;font-size:16px">Please Login to tron account for viewing your bets</p>
      </td>
    </tr>
  </tbody>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "my-bets",
  data() {
    return {
      loggedIn: false
    };
  },
  computed: {
    ...mapState([
      "allMyBets",
      "tronWeb",
      "betTabs",
      "tronLoggedIn",
      "tronAddress"
    ]),
    checkTronLogin() {
      return this.tronLoggedIn;
    },
    checkTronAddress() {
      return this.tronAddress;
    }
  },
  watch: {
    async checkTronLogin(value) {
      console.log("Value 1 value : " + value);

      if (value) {
        const nonHex = this.tronWeb.address.fromHex(
          (await this.tronWeb.trx.getAccount()).address.toString()
        );

        this.loggedIn = true;
        this.$store.dispatch("getAllMyBets", {
          tronaddress: nonHex
        });
      }
    },
    async checkTronAddress(value) {
      console.log("Value2 value : " + value);

      if (!value == "none") {
        const nonHex = this.tronWeb.address.fromHex(value);

        console.log("New address is this one : " + nonHex);

        this.loggedIn = true;
        this.$store.dispatch("getAllMyBets", {
          tronaddress: nonHex
        });
      }
    }
  },
  async mounted() {
    await this.$store.dispatch("getTronWebData");

    this.$store.dispatch("getAllMyBets");

    await this.$store.dispatch("getLogin");

    await this.$store.dispatch("getTronAddress");

    var addressCheck = "";

    if (await this.tronWeb.trx) {
      console.log("My bets: " + "true");
      console.log("My bets: " + (await this.tronWeb.trx.getAccount()).address);
      addressCheck = (await this.tronWeb.trx.getAccount()).address;
    } else {
      console.log("My bets: " + "False");
    }

    if (!addressCheck == "") {
      console.log("Address is not empty");

      const nonHex = this.tronWeb.address.fromHex(
        (await this.tronWeb.trx.getAccount()).address.toString()
      );

      this.loggedIn = true;
      this.$store.dispatch("getAllMyBets", {
        tronaddress: nonHex
      });

      console.log("data", nonHex);
    }
    // Set initial value
  },
  methods: {}
};
</script>

