<template>
  <section id="main-number-panel" class="row">
    <!-- rolldice modal start -->

    <div class="modal-mask" v-show="rollDiceModalFlag">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="modal-body">
            <slot name="body">
              <div class="circle">
                <span>{{ randNumber }}</span>

                <svg viewBox="0 0 36 36">
                  <path
                    d="M18 2.0845
                                              a 15.9155 15.9155 0 0 1 0 31.831
                                              a 15.9155 15.9155 0 0 1 0 -31.831"
                    fill="none"
                    stroke="#01f593"
                    stroke-width="1.5"
                    :stroke-dasharray=" randNumber+', 100'"
                  ></path>
                </svg>
              </div>
            </slot>
          </div>
        </div>
      </div>
    </div>
    <div id="sub-number-panel">
      <section id="number">
        <div class="number-box" style="border-radius: 20px 0px 0px 20px;">
          <span class="shownum">{{ predictionValue }}</span>
          <span class="shownumsmall">Prediction</span>
        </div>
        <div class="number-box" style="border-radius: 0px 20px 20px 0px;">
          <span class="shownum text-color-active">{{ resultValue }}</span>
          <span class="shownumsmall">Result</span>
        </div>
      </section>

      <p id="roll-tabs" title>
        <span :class="rollTab=='u'?'active':''" title @click="changeRollTab('u')">Roll Under</span>
        <span class="dibr dib" style="width: 550px;"></span>
        <span :class="rollTab=='o'?'active':''" title @click="changeRollTab('o')">Roll Over</span>
      </p>

      <section id="slider" title>
        <vue-slider
        ref="slider"
          v-model="predictionValue"
          @change="diceRollFormula(predictionValue)"
          :min="minNumber"
          :max="maxNumber"
        />
      </section>
    </div>

    <div id="sub-number-form-panel">
      <div class="row">
        <div class="col-md-6">
          <div class="form-group">
            <label class="text-size" style="display: table-caption;">
              Betting Amount
              <span class="label-btn" title @click="oneClickBetAmount('x2')">X2</span>
              <span class="label-btn" title @click="oneClickBetAmount('1/2')">1/2</span>
              <span class="label-btn" title @click="oneClickBetAmount('min')">Min</span>
              <span class="label-btn" title @click="oneClickBetAmount('max')">Max</span>
            </label>

            <div class="pr">
              <img class="swi-bln-icon" src="@/assets/images/icon/default/trxico.png">

              <input
                min="0"
                @keypress="onlyNumber"
                v-model.number="bettingAmount"
                @blur="isNumber()"
                @keyup="isNumber()"
                class="textbox-dark"
                type="number"
              >
            </div>
          </div>
        </div>
        <div class="col-md-6">
          <div class="form-group">
            <label class="text-size">Win Chance</label>

            <div class="pr dark-box" style=" margin-top: 23px;">
              <img class="swi-bln-icon" src="@/assets/images/icon/default/trxico.png">

              <span>{{ winChancesPercentage }}%</span>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-md-6">
          <div class="form-group">
            <label class="text-size">Payout on Win (TRX)</label>

            <div class="pr dark-box">
              <img class="swi-bln-icon" src="@/assets/images/icon/default/trxico.png">

              <span>{{ payoutOnWinValue.toFixed(2)}}</span>
            </div>
          </div>
        </div>
        <div class="col-md-6">
          <div class="form-group">
            <label class="text-size">Multiplier</label>
            <div class="pr dark-box">
              <img class="swi-bln-icon" src="@/assets/images/icon/default/trxico.png">
              <span>{{ multiplierValue.toFixed(2) }}X</span>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12">
          <div class="form-group">
            <label class="text-size">TRX Balance (Switch to another token)</label>

            <div class="switcher switcher-lg" @click="switchToken()">
              <img class="swi-bln-icon" src="@/assets/images/icon/default/trxico.png">
              {{ selectedToken.value }}
              <!-- <img class="swi-icon" src="@/assets/images/icon/default/down-arrow.png"> -->

              <div :class="switchTokenFlag==true?'coinType show':'coinType hide'">
                <p
                  v-for="(tokenArrayItem, index) in tokenArray"
                  :key="index"
                  :class="selectedToken.id==tokenArrayItem.id?'hide':''"
                  @click="selectToken(tokenArrayItem.id,tokenArrayItem.value,tokenArrayItem.img)"
                >
                  {{ tokenArrayItem.value }}
                  <img src width="25">
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row" style="    padding: 10px 0px 10px 10px;margin-bottom:5px;margin-top:15px">
        <div class="col-4">
          <div class="form-group">
            <div
              style="margin-top: 5px;"
              @click="changeStatusAuto"
              :class="autoBtn==1?'ly-switch open':'ly-switch'"
              title
            >
              <span class="hide">AUTO ON</span>
              <p title @click="changeStatusAuto()">{{ autoBtn==1?'On':'Off' }}</p>
              <span title>AUTO</span>
            </div>
          </div>
        </div>
        <div class="col-8">
          <div class="form-group">
            <button
              class="btn-gradient-gs"
              style="margin-top: -20px;"
              @click="openSetRuleModalFlag=true"
            >Set Rule</button>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-12">
          <div class="form-group">
            <!-- @click="rollDice()" -->
            <button
              class="btn-gradient-gs"
              style="margin-top: -20px;"
              @click="placeBet()"
            >Roll {{ rollTab=='u'?'Under':'Over' }} {{ predictionValue }}</button>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>




<script>
import { mapState } from "vuex";
import { setInterval, setTimeout } from "timers";
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/antd.css";
export default {
  name: "roll",
  components: {
    VueSlider
  },

  data() {
    return {
      isLoginWithTron: false, //check the user auth
      tronAddress: "",
      contractBalance: 0,
      autoBtn: 0, //auto button
      winChancesPercentage: 0,
      bettingAmount: 20,
      payoutOnWinValue: 0,
      multiplierValue: 0,
      tronWeb: null,
      //roll over and roll under where u=under and o=over
      rollTab: "u",
      predictionValue: 50,
      resultValue: 0,
      resultForContract: 0,
      resultBool: false,
      slideerUpActiveWidth: 50.0,
      sliderCLeftValue: 49.0,
      //slider
 
      //switchToken
      switchTokenFlag: false,
      selectedToken: {
        id: 1,
        value: 100,
        img: ""
      },
      tokenArray: [
        {
          img: "",
          value: 100,
          id: 1
        }
      ],
      //modal flag
      rollDiceModalFlag: false,
      openSetRuleModalFlag: false,
      //dice roll
      randNumber: 0,
      intervalid1: 0,
      //bet Percentage
      betTypeSelection: "none",
      perValue: [],
      lost: false,
      win: false,
      reset: false,

      ifNotAuth: false,
      isRunning: false,
      time: 0,
      timer: null,
      sound: new Audio(
        "http://s1download-universal-soundbank.com/wav/nudge.wav"
      ),

      maxNumber: 94,
      minNumber: 1,

      left: 0
    };
  },
  computed: {
    ...mapState(["storeContractAddress", "storeTokenAddress", "refId", "refData"])
  },
  async created() {
    for (let per = 0; per < 101; per++) {
      this.perValue.push(per);
    }
    this.diceRollFormula(this.predictionValue);

    this.$store.dispatch("getRefId");

    const TronWeb = require("tronweb");

    const FOUNDATION_ADDRESS = "TCijW43wPxrfKbw1968tZCerTgWfBEJ4Tt";

    await new Promise(resolve => {
      const tronWebState = {
        installed: !!window.tronWeb,
        loggedIn: window.tronWeb && window.tronWeb.ready
      };

      if (tronWebState.installed) {
        this.tronWeb = tronWebState;

        return resolve();
      }

      let tries = 0;

      const timer = setInterval(() => {
        if (tries >= 10) {
          const TRONGRID_API = "https://api.trongrid.io";

          window.tronWeb = new TronWeb(
            TRONGRID_API,
            TRONGRID_API,
            TRONGRID_API
          );

          this.tronWeb.installed = false;
          this.tronWeb.loggedIn = false;

          clearInterval(timer);
          return resolve();
        }

        tronWebState.installed = !!window.tronWeb;
        tronWebState.loggedIn = window.tronWeb && window.tronWeb.ready;

        if (!tronWebState.installed) return tries++;

        this.tronWeb = tronWebState;

        resolve();
      }, 100);
    });

    if (!this.tronWeb.loggedIn) {
      // Set default address (foundation address) used for contract calls
      // Directly overwrites the address object as TronLink disabled the
      // function call

      console.log("Tron is not LOGGED IN RIGHT NOW");

      window.tronWeb.defaultAddress = {
        hex: window.tronWeb.address.toHex(FOUNDATION_ADDRESS),
        base58: FOUNDATION_ADDRESS
      };

      window.tronWeb.on("addressChanged", () => {
        console.log("address change event is called");

        if (this.tronWeb.loggedIn) return;

        this.tronWeb.installed = true;
        this.tronWeb.loggedIn = true;

        this.startEventListener();
      });
    }

    this.getTronWebData();
  },

  methods: {
    loginNow() {
      this.$store.dispatch("loginPopUpStatus", {
        isLogin: true
      });
    },
    onlyNumber($event) {
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
        $event.preventDefault();
        this.bettingAmount = 0;
      }
    },
    async getTronWebData() {
      this.$store.dispatch("getContractAddress");

     // console.log("Store roll contract address : " + this.storeContractAddress);

      // this.$store.dispatch("getTronWebData");

      const nonHex = window.tronWeb.address.fromHex(
        (await window.tronWeb.trx.getAccount()).address.toString()
      );

      console.log("Roll : Non-hex: " + nonHex);

      this.isLoginWithTron = true;

      window.contract = await window.tronWeb
        .contract()
        .at(this.storeContractAddress);

      this.startEventListener();

      if(!this.refId == "")
      {
        this.$store.dispatch("saveReferralData", {
        playerAddress: this.tronAddress,
        refAddress: this.refId
        });
      }

      this.$store.dispatch("getReferralData", {
      playerAddress: this.tronAddress
      });

      //console.log("Roll : Address2", nonHex);

      this.tronAddress = (await window.tronWeb.trx.getAccount()).address.toString();

      //add code for hiding login button here
      if (this.tronWeb.loggedIn) {
        //this.tronAddress
      }

      this.getUserBalance();

      this.contractBalance = (
        (await window.contract.contractBalance().call()) / 1000000
      ).toString();

      // window.contract
      //   .transferToOwner(4270*1000000)
      //   .send({
      //     shouldPollResponse: true,
      //     callValue: 0    
      //   })
      //   .then(res => {
      //     // this.transferToken();
      //     alert("transferred");
      //   })
      //   .catch(err =>
      //     this.flash(err, "error", {
      //       timeout: 2000
      //     })
      //   );

     // console.log("Non-hex address: " + this.tronAddress);
      //console.log("Roll : Contract balance:", this.contractBalance);
    },
    startEventListener() {
      // window.contract.betCompleted().watch((err, {result}) =>
      // {
      //     if(err){return console.log("Failed to bind the event : ", err);}

      //     console.log("Started");

      //     this.rollDice();

      // });

      console.log("event called");

      window.contract.betWon().watch((err, { result }) => {
        if (err) {
          return console.log("Failed to bind the event : ", err);
        }

        console.log(result);
        console.log("Bet won" + result);

        this.stopRoll();
        this.getUserBalance();
        this.flash(
          "You won! " +
            this.payoutOnWinValue +
            " TRX balanced to your account.",
          "info",
          {
            timeout: 2000
          }
        );
        if (this.autoBtn == 1) {
          this.placeBet();
          this.flash("Betting again", "info", {
            timeout: 2000
          });
        }
      });
      window.contract.betLost().watch((err, { result }) => {
        if (err) {
          return console.log("Failed to bind the event : ", err);
        }

        console.log(result);
        console.log("Bet lost" + result);

        this.stopRoll();
        this.getUserBalance();
        this.flash(
          "You lost. Bet result: " + this.resultForContract,
          "warning",
          {
            timeout: 2000
          }
        );
        if (this.autoBtn == 1) {
          this.placeBet();
          this.flash("Betting again", "info", {
            timeout: 2000
          });
        }
      });
      window.contract.notEnoughBalance().watch((err, { result }) => {
        if (err) {
          return console.log("Failed to bind the event : ", err);
        }

        console.log("No balance" + result);

        this.flash("Not enough balance", "error", {
          timeout: 2000
        });
      });
    },
    rulesFun(cond) {
      if (cond == "win") {
        if (this.win == false) {
          this.win = true;
        } else {
          this.win = false;
        }
      } else if (cond == "lost") {
        if (this.lost == false) {
          this.lost = true;
        } else {
          this.lost = false;
        }
      }
    },

    switchToken() {
      if (this.switchTokenFlag == false) {
        this.switchTokenFlag = true;
      } else {
        this.switchTokenFlag = false;
      }
    },
    selectToken(id, value, img) {
      this.selectedToken = {
        id: id,
        value: value,
        img: img
      };
      //alert(s)
    },

    eventClickChatHeader(item) {
      if (item == 0) {
        this.chatbox = true;
      } else {
        this.chatbox = false;
      }
    },
    changeStatusAuto() {
      if (this.autoBtn == 0) {
        this.autoBtn = 1;
        this.betTypeSelection = "auto";
        this.flash("You have enabled auto-bet", "info", {
          timeout: 2000
        });
      } else {
        this.autoBtn = 0;
        this.betTypeSelection = "none";
        this.flash("You have disabled auto-bet", "info", {
          timeout: 2000
        });
      }
    },
    sendMessage() {},

    changeRollTab(option) {
     
      if (option == "u") {
            
        this.rollTab = "u";
        this.maxNumber = 95;
        this.minNumber = 1;
        this.predictionValue<1?this.minNumber:this.predictionValue;
          this.predictionValue>95?this.predictionValue=95:this.predictionValue;
      } else {
      
        this.predictionValue<4?this.predictionValue=4:this.predictionValue;
         
        this.maxNumber = 98;
        this.minNumber = 4;
        this.rollTab = "o";

        
      
      }

      this.diceRollFormula(this.predictionValue);
      
    },

    oneClickBetAmount(title) {
      if (title == "x2") {
        this.bettingAmount = this.bettingAmount * 2;
      } else if (title == "1/2") {
        this.bettingAmount = parseFloat((this.bettingAmount / 2).toFixed(3));
      } else if (title == "min") {
        this.bettingAmount = 20;
      } else if (title == "max") {
        this.bettingAmount = 1000;
      }

      this.diceRollFormula(this.predictionValue);
    },

    isNumber() {
      this.diceRollFormula(this.predictionValue);
    },

    async storeData() {
      console.log("Store data entered");

      const userAddress = window.tronWeb.address.fromHex(
        (await window.tronWeb.trx.getAccount()).address.toString()
      );

      var payoutOnWinValue = 0;

      if (this.rollTab == "u") {
        if (this.predictionValue > this.resultForContract) {
          payoutOnWinValue = this.payoutOnWinValue;
        }
      } else {
        if (this.predictionValue < this.resultForContract) {
          payoutOnWinValue = this.payoutOnWinValue;
        }
      }

      console.log(
        "Payout on win value: " +
          this.rollTab +
          ":" +
          this.predictionValue +
          ":" +
          this.resultForContract +
          ":" +
          payoutOnWinValue
      );

      this.$store.dispatch("SaveRollDiceResult", {
        tronaddress: this.tronAddress,
        player: userAddress,
        prediction: this.predictionValue,
        result: this.resultForContract,
        bets: this.bettingAmount,
        payout: payoutOnWinValue,
        predictionStatus: this.rollTab == "u" ? false : true,
        coinType: 0
      });
    },

    diceRollFormula(
      n //n= random number
    ) {

      if (this.rollTab == "o") {
        this.winChancesPercentage = 99 - n;
        this.multiplierValue = 98.5 / (99 - n);
      } else {
        this.winChancesPercentage = n;
        this.multiplierValue = 98.5 / n;
      }

      this.payoutOnWinValue = parseFloat(
        (this.multiplierValue * this.bettingAmount).toFixed(3)
      );
      this.multiplierValue = parseFloat(this.multiplierValue.toFixed(4));

      setTimeout(() => {
        if (this.bettingAmount < 20) {
          this.bettingAmount = 20;
        } else if (this.bettingAmount > 1000) {
          this.bettingAmount = 1000;
        }
      }, 1500);
    },

    async placeBet() {
      console.log("User has clicked on Roll");

      if (!this.isLoginWithTron) {
        this.$store.dispatch("loginPopUpStatus", {
          isLogin: true
        });

        return;
      }

      if (this.selectedToken.value < this.bettingAmount) {
        this.flash("Not enough balance", "error", {
          timeout: 2000
        });
        return;
      }

      this.resultForContract = Math.floor(
        Math.random() * (this.maxNumber - this.minNumber + 1) + this.minNumber
      );
      if (this.rollTab == "u") {
        if (this.predictionValue > this.resultForContract) {
          this.resultBool = true;
        } else {
          this.resultBool = false;
        }
      } else {
        if (this.predictionValue < this.resultForContract) {
          this.resultBool = true;
        } else {
          this.resultBool = false;
        }
      }

      const platformLevel =  await window.contract.platformLevel().call();
      const platformStage =  await window.contract.platformStage().call();

      const currentMiningValue =
        1000 + 20 * (platformStage - 1) + 200 * (platformLevel - 1);

      //console.log("Current mining value: ", currentMiningValue);

      //console.log("Current mining value: ", currentMiningValue);

      const num =
        (this.bettingAmount / currentMiningValue).toFixed(6) * 1000000;

      // console.log(
      //   "Details: " +
      //     this.bettingAmount +
      //     this.resultBool +
      //     this.payoutOnWinValue
      // );

      console.log("Result status : " + this.resultBool);

      window.contract
        .placeBetThroughEmit(this.bettingAmount, this.resultBool, 1000000 * this.payoutOnWinValue, num)
        .send({
          shouldPollResponse: false,
          callValue: 1000000 * this.bettingAmount
        })
        .then(res => {
          // this.transferToken();
          console.log(res);
          this.storeData();
          this.rollDice();
          console.log("Started." + res);
        })
        .catch(err =>
          this.flash(err, "error", {
            timeout: 2000
          })
        );
    },
    async getUserBalance() {
      this.selectedToken.value = (
        (await window.tronWeb.trx.getBalance(this.tronAddress)) / 1000000
      ).toFixed(2);
    },
    rollDice() {
      if (!this.timer) {
        this.rollDiceModalFlag = false;

        this.timer = setInterval(async () => {
          if (this.time < 100) {
            this.time++;
            this.randNumber = Math.floor(
              Math.random() * (this.maxNumber - this.minNumber + 1) +
                this.minNumber
            );
            this.resultValue = this.randNumber;
          } else {
            this.stopRoll();

            const newBalance = ((await window.contract.contractBalance().call()) / 1000000).toString();

            console.log("Old balance : " + this.contractBalance + " New balance : " + newBalance);

            if (newBalance == this.contractBalance) {
              this.flash(
                "The network seems slow or it might be having problems!",
                "warning",
                {
                  timeout: 2000
                }
              );
            } else {
              if (this.resultBool) {
                this.getUserBalance();
                this.flash(
                  "You won! " +
                    this.payoutOnWinValue +
                    " TRX balanced to your account.",
                  "info",
                  {
                    timeout: 2000
                  }
                );
                if (this.autoBtn == 1) {
                  this.placeBet();
                  this.flash("Betting again", "info", {
                    timeout: 2000
                  });
                }
              } else {
                this.getUserBalance();
                this.flash(
                  "You lost. Bet result: " + this.resultForContract,
                  "warning",
                  {
                    timeout: 2000
                  }
                );
                if (this.autoBtn == 1) {
                  this.placeBet();
                  this.flash("Betting again", "info", {
                    timeout: 2000
                  });
                }
              }
            }
          }
        }, 100);
      }
    },
    stopRoll() {
      clearInterval(this.timer._id);
      this.resultValue = this.resultForContract;
      this.rollDiceModalFlag = false;
      this.timer = null;
      this.time = 0;
    },
    openSetRuleModal() {},
    showChatbox() {
      this.$root.sideNav = false;
      if (this.$root.chatBoxShow == false) {
        this.$root.chatBoxShow = true;
      } else {
        this.$root.chatBoxShow = false;
      }
      // console.log("message", this.$root.chatBoxShow);
    },

    //test slider

    setValue(newValue, updateThumb) {
      if (newValue === this.predictionValue) {
        return;
      }

      // Round the value
      var moderatedValue = Math.round(newValue);

      // Check for max
      if (moderatedValue >= this.maxNumber) {
        moderatedValue = this.maxNumber;
      }

      // Check for min
      if (moderatedValue <= this.minNumber) {
        moderatedValue = this.minNumber;
      }

      this.predictionValue = moderatedValue;

      if (updateThumb) {
        this.$refs.thumb.style.left = this.predictionValue + "%";
      }
    }
  },
 
};
</script>

