<template>
  <section>
    <!-- Button trigger modal -->

    <div class="modal-mask" v-show="isLogin" style=" z-index: 100000000;">
      <div class="modal-wrapper">
        <div class="modal-container bg-color" style="border:3px solid; border-radius:10px">
          <div
            class="modal-header"
            style="margin-bottom: -18px;text-align: center;display: block;border-bottom: 0px;"
          >
            <div class="dividends-header" style="position:relative">
              <div
                @click="changeLoginPopStatus()"
                style="border-radius: 50%;position: absolute;right: -20px;top: -48px; width: 40px;height: 40px;cursor:pointer;  background-image: linear-gradient(#438d6a, #808080)"
              >
                <img
                  style="position: absolute;margin: auto;width: 15px;/* margin: 13px;left: 0;right: 0;top: 0;bottom: 0;"
                  src="@/assets/images/icon/default/close.png"
                >
              </div>
            </div>
          </div>

          <div
            class="modal-body"
            style=" padding: 0.2rem 0rem 0.2rem 0rem;display:grid;border-bottom:0px"
          >
            <p>
              Please login to your TRONlink wallet. If you do not have TRONLink wallet installed please visit http://u6.gg/gmc5D and download the chrome extension. (
              <a href="http://tronlink.org">TRONlink</a> is only availble using Chrome browser for the time
              being)
            </p>
          </div>
        </div>
      </div>
    </div>

    <flash-message class="myCustomClass" style="color:white"></flash-message>
    <!-- setrule modal start -->
    <div class="modal-mask" v-show="openSetRuleModalFlag" style=" z-index: 99999999;">
      <div class="modal-wrapper">
        <div class="modal-container bg-color">
          <div class="modal-header" style="display: block;">
            <slot name="header">
              <p>Set Rule</p>
              <p>Note: In set rule, you can increase or decrease bet amount percentage</p>
            </slot>
          </div>

          <div class="modal-body" style=" padding: 0.2rem 0rem 0.2rem 0rem;display:grid">
            <slot name="body">
              <ul class="bet-selection">
                <li>
                  <input type="radio" class="form-checker" value="auto" v-model="betTypeSelection"> Auto Bet
                </li>
                <li>
                  <input
                    type="radio"
                    class="form-checker"
                    value="normal"
                    v-model="betTypeSelection"
                  > Normal Bet
                </li>
                <li>
                  <input type="radio" class="form-checker" value="both" v-model="betTypeSelection"> Both
                </li>
                <li>
                  <input type="radio" class="form-checker" value="none" v-model="betTypeSelection"> None
                </li>
              </ul>

              <ul class="bet-selection-option" v-if="betTypeSelection!='none'">
                <li>
                  <input
                    type="checkbox"
                    class="form-checker"
                    @click="rulesFun('lost')"
                    name="bet-type-rule-selection"
                  >
                  <span>
                    If lost, increase bet by
                    <select :v-if="lost==false?'disabled':'enabled'">
                      <option v-for="(perValueItem, index) in perValue" :key="index">{{ index }}</option>
                    </select>
                    % to previous bet amount
                  </span>
                </li>
                <li>
                  <input
                    type="checkbox"
                    class="form-checker"
                    @click="rulesFun('win')"
                    name="bet-type-rule-selection"
                  >
                  <span>If won, start from initial bet amount</span>
                </li>
              </ul>
            </slot>
          </div>

          <div class="modal-footer">
            <slot name="footer">
              <button class="modal-default-button" @click="openSetRuleModalFlag=false">OK</button>
            </slot>
          </div>
        </div>
      </div>
    </div>

    <section id="chat-box" :class="showChatPanel?'show':'hide'">
      <div id="chat-box-header">
        <p :class="chatTabs==0?'active':''" title @click="changeChatTabs(0)">
          <img v-show="chatTabs==1" src="@/assets/images/icon/default/message.png" width="40px">
          <img v-show="chatTabs==0" src="@/assets/images/icon/active/message.png" width="40px">
        </p>
        <p :class="chatTabs==1?'active':''" title @click="changeChatTabs(1)">
          <img v-show="chatTabs==0" src="@/assets/images/icon/default/leaderboard.png" width="40px">
          <img v-show="chatTabs==1" src="@/assets/images/icon/active/leaderboard.png" width="40px">
        </p>
      </div>

      <chat></chat>
      <rank></rank>
    </section>

    <roll></roll>

    <section id="sub-data-panel">
      <div class="data-tab">
        <ul>
          <li :class="betTabs==0?'active':''" @click="changeBetTab(0)">
            <a style="cursor:pointer">My Bets</a>
          </li>
          <li :class="betTabs==1?'active':''" @click="changeBetTab(1)">
            <a style="cursor:pointer">All Bets</a>
          </li>
          <li :class="betTabs==2?'active':''" @click="changeBetTab(2)">
            <a style="cursor:pointer">High Rollers</a>
          </li>
          <li :class="betTabs==3?'active':''" @click="changeBetTab(3)">
            <a style="cursor:pointer">Rare Wins</a>
          </li>
        </ul>
      </div>

      <table cellspacing="0" cellpadding="0" id="list-table">
        <tr>
          <th class="adj-th" style="width: 100px;"></th>
          <!-- react-text: 690 -->
          <!-- /react-text -->
          <th class="adj-th list-th" style="width: 210px;" title>Player</th>
          <th class="list-th" title>Prediction</th>
          <th class="list-th" style="width: 20%;" title>Result</th>
          <th class="list-th" style="text-align: right; width: 10%;">Bets</th>
          <th class="list-th" style="text-align: right; width: 20%;">Payout</th>
          <th class="adj-th list-th" style="width: 150px;"></th>
        </tr>

        <!-- mybets component -->

        <my-bets></my-bets>

        <!-- allbets component -->

        <all-bets></all-bets>
        <!-- high rollers bets components -->
        <high-rollers-bets></high-rollers-bets>

        <!-- rare wins bets components -->
        <rare-wins-bets></rare-wins-bets>
      </table>
    </section>

    <section id="footer-message-small">
      <img
        class="header-message"
        @click="showChatbox()"
        src="@/assets/images/icon/default/message.png"
        alt
      >
    </section>
  </section>
</template>

<script>
require("vue-flash-message/dist/vue-flash-message.min.css");
import { mapState, mapMutations } from "vuex";
import AllBets from "@/components/dapps/dice/betshistorypanel/allbets";
import HighRollersBets from "@/components/dapps/dice/betshistorypanel/highrollersbets";
import RareWinsBets from "@/components/dapps/dice/betshistorypanel/rarewinsbets";
import MyBets from "@/components/dapps/dice/betshistorypanel/mybets";

import Chat from "@/components/dapps/dice/chatpanel/chat";
import Rank from "@/components/dapps/dice/chatpanel/rank";

import Roll from "@/components/dapps/dice/rollpanel/roll";

export default {
  components: {
    AllBets,
    HighRollersBets,
    RareWinsBets,
    MyBets,
    Chat,
    Rank,
    Roll
  },

  data() {
    return {
      isLoginWithTron: false, //check the user auth
      tronAddress: "", //tron account address
      chatbox: true, //chat box header tab

      autoBtn: 0, //auto button

      winChancesPercentage: 0,
      bettingAmount: 10,
      payoutOnWinValue: 0,
      multiplierValue: 0,

      //roll over and roll under where u=under and o=over
      rollTab: "u",

      predictionValue: 50,
      resutlValue: 0,
      slideerUpActiveWidth: 50.0,
      sliderCLeftValue: 49.0,

      //slider
      dragging: false,
      draggable: null,
      keyboardStep: 1,
      minRangeSliderValue: 1,
      maxRangeSliderValue: 100,

      //switchToken
      switchTokenFlag: false,
      selectedToken: {
        id: 1,
        value: 100,
        img: ""
      },

      tokenArray: [
        {
          img: "",
          value: 100,
          id: 1
        }
      ],

      //modal flag
      rollDiceModalFlag: false,
      openSetRuleModalFlag: false,
      //dice roll
      randNumber: 0,
      intervalid1: 0,

      //bet Percentage
      betTypeSelection: "none",
      perValue: [],
      lost: false,
      win: false,
      reset: false
    };
  },

  computed: {
    ...mapState([
      "allBets",
      "betTabs",
      "chatTabs",
      "tronAuth",
      "isLogin",
      "showChatPanel",
      "refId"
    ])
  },

  created() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();

    this.$store.dispatch("saveReferralID", {
        refId: this.$route.params.id
    });

    if (this.windowWidth > 991) {
      this.$store.dispatch("chatPanelStatus", {
        status: true
      });
    } else {
      this.$store.dispatch("chatPanelStatus", {
        status: false
      });
    }

    for (let per = 0; per < 101; per++) {
      this.perValue.push(per);
    }
  },

  methods: {
    showChatbox() {
      if (this.showChatPanel) {
        this.$store.dispatch("chatPanelStatus", {
          status: false
        });
      } else {
        this.$store.dispatch("chatPanelStatus", {
          status: true
        });
      }
    },
    show() {
      this.$modal.show("hello-world");
    },
    hide() {
      this.$modal.hide("hello-world");
    },

    changeLoginPopStatus() {
      this.$store.dispatch("loginPopUpStatus", {
        isLogin: false
      });
    },

    ...mapMutations(["CHANGE_BET_TAB", "CHANGE_CHAT_TAB"]),
    changeBetTab: function(status) {
      this.CHANGE_BET_TAB(status);
      switch (status) {
        case 0:
          this.$store.dispatch("getAllMyBets");
          break;
        case 1:
          this.$store.dispatch("getAllBets");
          break;
        case 2:
          this.$store.dispatch("getHighRollersBets");
          break;
        case 3:
          this.$store.dispatch("getRareWinsBets");
          break;
      }
    },
    changeChatTabs: function(status) {
      this.CHANGE_CHAT_TAB(status);
    },

    //only number
    onlyNumber($event) {
      //console.log($event.keyCode); //keyCodes value
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
        // 46 is dot
        $event.preventDefault();
        this.bettingAmount = 0;
      }
    },
    handleResize() {
      this.windowHeight = window.innerHeight;
      this.windowWidth = window.innerWidth;
      if (this.windowWidth > 991) {
        this.showChatPanel = true;
      } else {
        this.showChatPanel = false;
      }
    }
  }
};
</script>


