<template>
  <section>
    <section id="chat-box" :class="showChatPanel?'show':'hide'">
      <div id="chat-box-header">
        <p :class="chatTabs==0?'active':''" title @click="changeChatTabs(0)">
          <img v-show="chatTabs==1" src="@/assets/images/icon/default/message.png" width="40px">
          <img v-show="chatTabs==0" src="@/assets/images/icon/active/message.png" width="40px">
        </p>
        <p :class="chatTabs==1?'active':''" title @click="changeChatTabs(1)">
          <img v-show="chatTabs==0" src="@/assets/images/icon/default/leaderboard.png" width="40px">
          <img v-show="chatTabs==1" src="@/assets/images/icon/active/leaderboard.png" width="40px">
        </p>
      </div>
      <chat></chat>
      <rank></rank>
    </section>
  
    <div id="crownSection">
      <PlayCrown></PlayCrown>
    </div>


    <div id="crownBoardSection">
      <Board></Board>
    </div>
  
  
    <section id="footer-message-small">
      <img class="header-message" @click="showChatbox()" src="@/assets/images/icon/default/message.png" alt>
    </section>
  </section>
</template>

<script>
  require("vue-flash-message/dist/vue-flash-message.min.css");
  import {
    mapState,
    mapMutations
  } from "vuex";
  import Chat from "@/components/dapps/dice/chatpanel/chat";
  import Rank from "@/components/dapps/dice/chatpanel/rank";
  import PlayCrown from "@/components/dapps/crown/playcrown";
  import Board from "@/components/dapps/crown/board/board";
 
  
  export default {
  
    components: {
  
      Chat,
      Rank,
      PlayCrown,
      Board
  
    },
  
    created() {
      window.addEventListener("resize", this.handleResize);
      this.handleResize();
  
      if (this.windowWidth > 991) {
        this.$store.dispatch("chatPanelStatus", {
          status: true
        });
      } else {
        this.$store.dispatch("chatPanelStatus", {
          status: false
        });
      }
  
      for (let per = 0; per < 101; per++) {
        this.perValue.push(per);
      }
    },
    computed: {
      ...mapState([
        "allBets",
        "betTabs",
        "chatTabs",
        "tronAuth",
        "isLogin",
        "showChatPanel"
      ])
    },
    methods: {
      ...mapMutations(["CHANGE_CHAT_TAB"]),
      showChatbox() {
        if (this.showChatPanel) {
          this.$store.dispatch("chatPanelStatus", {
            status: false
          });
        } else {
          this.$store.dispatch("chatPanelStatus", {
            status: true
          });
        }
      },
      changeChatTabs: function(status) {
        this.CHANGE_CHAT_TAB(status);
      },
      handleResize() {
        this.windowHeight = window.innerHeight;
        this.windowWidth = window.innerWidth;
        if (this.windowWidth > 991) {
          this.showChatPanel = true;
        } else {
          this.showChatPanel = false;
        }
      }
    }
  
  
  }
</script>

