<script>
  import {
    setInterval,
    setTimeout
  } from "timers";
  import {
      mapState
  } from "vuex";
  export default {
    data() {
      return {
        HashID: '8f002b8147977295a04c4133b51e238f159d20ff3390f4159a82a08a3a3092c3', //default 
        timer: null,
        dice: ["top", "bottom", "right", "left", "front", "back"],
        crownBettingAmount: 10,
        totalBetAmount:0,
        switchTokenFlag: true,
        maxNumber: 5,
        minNumber: 0,
        firstDice: 0,
        secondDice: 0,
        thirdDice: 0,
        fouthDice: 0,
        fifthDice: 0,
        sixthDice: 0,
        rollClass0: "",
        rollClass1: "",
        rollClass2: "",
        rollClass3: "",
        rollClass4: "",
        rollClass5: "",
        loadingRoll: false,
        rollCompleted: false,
        sound: true,
        tokenArray: [{
          img: "",
          value: 100,
          id: 1
        }],
        selectedToken: {
          id: 1,
          value: 100,
          img: ""
        }
      };
    },
    computed:{
     ...mapState(['crownBetValue'])
    },
    created() {
      console.log(this.crownBetValue.bValue);
    },
    methods: {
      funtotalBetAmount(){
       
        var tba = this.crownBetValue.bValue+this.crownBetValue.cValue+this.crownBetValue.jValue+this.crownBetValue.eValue+this.crownBetValue.pValue+this.crownBetValue.sValue ;
        this.totalBetAmount = tba;
 
      },
      changeBettingAmountValue(dice){
        var value = 0;

        switch (dice) {
          case 'b':
            value = this.crownBettingAmount + this.crownBetValue.bValue;
            break;
          case 'c':
             value = this.crownBettingAmount + this.crownBetValue.cValue;
            break;
          case 'j':
             value = this.crownBettingAmount + this.crownBetValue.jValue;
            break;
          case 'e':
             value = this.crownBettingAmount + this.crownBetValue.eValue;
            break;
          case 'p':
             value = this.crownBettingAmount + this.crownBetValue.pValue;
            break;
          case 's':
             value = this.crownBettingAmount + this.crownBetValue.sValue;
            break;
        
          default:
            break;
        }

      
        this.$store.dispatch('changeBettingAmountValue',{
          value:value,
          dice:dice
        });

          this.funtotalBetAmount();
      
      },
      soundFun(sound) {
        switch (sound) {
          case 0:
            this.sound = false;
            break;
          case 1:
            this.sound = true;
            break;
        }
      },
      onlyNumber($event) {
        let keyCode = $event.keyCode ? $event.keyCode : $event.which;
        if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
          $event.preventDefault();
          this.crownBettingAmount = 0;
        }
      },
      oneClickBetAmount(title) {
   
      if (title == "x2") {
       if(this.crownBettingAmount>1000){
         this.crownBettingAmount=1000;
       }
       else{
          this.crownBettingAmount = this.crownBettingAmount * 2;
       }
      } else if (title == "1/2") {
      if(this.crownBettingAmount<=10){
           this.crownBettingAmount=10;
       }else{
          this.crownBettingAmount = parseFloat((this.crownBettingAmount / 2).toFixed(3));
       }
       
      } else if (title == "min") {
        this.crownBettingAmount = 20;
      } else if (title == "max") {
        this.crownBettingAmount = 1000;
      }
      },
  
      async roll() {
  
        if (this.loadingRoll == false) {
          this.loadingRoll = true;
          this.rollCompleted = false;
          var tries = 0;
          this.timer = await setInterval(() => {
            if (tries >= 50) {
              this.rollCompleted = true;
              var audio = new Audio('http://soundbible.com/mp3/Shake And Roll Dice-SoundBible.com-591494296.mp3') // path to file
              this.sound ? audio.play() : '';
  
              this.loadingRoll = false;
  
              clearInterval(this.timer._id);
  
            } else {
              //  var audio = new Audio('http://soundbible.com/mp3/Spray_Paint_Shake-Stephan_Schutze-1588335609.mp3') // path to file
              // audio.play()
              this.now();
            }
  
            console.log('number', tries);
            tries++
          }, 100);
          //         var audio = new Audio('');
          // audio.play();
  
  
        }
  
      },
      now() {
        this.firstDice = Math.floor(
          Math.random() * (this.maxNumber - this.minNumber + 1) + this.minNumber
        );
        this.secondDice = Math.floor(
          Math.random() * (this.maxNumber - this.minNumber + 1) + this.minNumber
        );
        this.thirdDice = Math.floor(
          Math.random() * (this.maxNumber - this.minNumber + 1) + this.minNumber
        );
        this.fouthDice = Math.floor(
          Math.random() * (this.maxNumber - this.minNumber + 1) + this.minNumber
        );
        this.fifthDice = Math.floor(
          Math.random() * (this.maxNumber - this.minNumber + 1) + this.minNumber
        );
        this.sixthDice = Math.floor(
          Math.random() * (this.maxNumber - this.minNumber + 1) + this.minNumber
        );
        // alert(this.firstDice);
        if (this.firstDice === 5) {
          this.rollClass0 = "show-bottom";
        } else if (this.firstDice === 4) {
          this.rollClass0 = "show-top";
        } else if (this.firstDice === 3) {
          this.rollClass0 = "show-left";
        } else if (this.firstDice === 2) {
          this.rollClass0 = "show-right";
        } else if (this.firstDice === 1) {
          this.rollClass0 = "show-back";
        } else if (this.firstDice === 0) {
          this.rollClass0 = "show-front";
        }
  
        if (this.secondDice === 5) {
          this.rollClass1 = "show-bottom";
        } else if (this.secondDice === 4) {
          this.rollClass1 = "show-top";
        } else if (this.secondDice === 3) {
          this.rollClass1 = "show-left";
        } else if (this.secondDice === 2) {
          this.rollClass1 = "show-right";
        } else if (this.secondDice === 1) {
          this.rollClass1 = "show-back";
        } else if (this.secondDice === 0) {
          this.rollClass1 = "show-front";
        }
  
        if (this.thirdDice === 5) {
          this.rollClass2 = "show-bottom";
        } else if (this.thirdDice === 4) {
          this.rollClass2 = "show-top";
        } else if (this.thirdDice === 3) {
          this.rollClass2 = "show-left";
        } else if (this.thirdDice === 2) {
          this.rollClass2 = "show-right";
        } else if (this.thirdDice === 1) {
          this.rollClass2 = "show-back";
        } else if (this.thirdDice === 0) {
          this.rollClass2 = "show-front";
        }
  
        if (this.fouthDice === 5) {
          this.rollClass3 = "show-bottom";
        } else if (this.fouthDice === 4) {
          this.rollClass3 = "show-top";
        } else if (this.fouthDice === 3) {
          this.rollClass3 = "show-left";
        } else if (this.fouthDice === 2) {
          this.rollClass3 = "show-right";
        } else if (this.fouthDice === 1) {
          this.rollClass3 = "show-back";
        } else if (this.fouthDice === 0) {
          this.rollClass3 = "show-front";
        }
  
        if (this.fifthDice === 5) {
          this.rollClass4 = "show-bottom";
        } else if (this.fifthDice === 4) {
          this.rollClass4 = "show-top";
        } else if (this.fifthDice === 3) {
          this.rollClass4 = "show-left";
        } else if (this.fifthDice === 2) {
          this.rollClass4 = "show-right";
        } else if (this.fifthDice === 1) {
          this.rollClass4 = "show-back";
        } else if (this.fifthDice === 0) {
          this.rollClass4 = "show-front";
        }
  
        if (this.sixthDice === 5) {
          this.rollClass5 = "show-bottom";
        } else if (this.sixthDice === 4) {
          this.rollClass5 = "show-top";
        } else if (this.sixthDice === 3) {
          this.rollClass5 = "show-left";
        } else if (this.sixthDice === 2) {
          this.rollClass5 = "show-right";
        } else if (this.sixthDice === 1) {
          this.rollClass5 = "show-back";
        } else if (this.sixthDice === 0) {
          this.rollClass5 = "show-front";
        }
      }
    }
  };
</script>

<style>
  .active-bet{
      border: 2.5px solid #438d6a;
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);
    position: absolute;
    width: 89px;
    height: 85px;
    /* background: green; */
    left: 0;
    right: 0;
    /* bottom: 0; */
    top: 4px;
    margin: auto;
    border-radius: 23px;
  }
  * {
    box-sizing: border-box;
  }
  
  body {
    font-family: sans-serif;
  }
  
  .scene {
    width: 120px;
    height: 120px;
    /* border: 1px solid #CCC; */
    margin: 0;
    perspective: 240px;
  }
  
  .cube {
    width: 120px;
    height: 120px;
    position: relative;
    transform-style: preserve-3d;
    transform: translateZ(-90px);
    transition: transform 0.25s;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);
  }
  
  .cube.show-front {
    transform: translateZ(-100px) rotateY(0deg);
  }
  
  .cube.show-right {
    transform: translateZ(-100px) rotateY(-90deg);
  }
  
  .cube.show-back {
    transform: translateZ(-100px) rotateY(-180deg);
  }
  
  .cube.show-left {
    transform: translateZ(-100px) rotateY(90deg);
  }
  
  .cube.show-top {
    transform: translateZ(-100px) rotateX(-90deg);
  }
  
  .cube.show-bottom {
    transform: translateZ(-100px) rotateX(90deg);
  }
  
