<script>
import { mapState } from "vuex";
import VueCountdown from '@chenfengyuan/vue-countdown';
import Vue from 'vue';

Vue.component(VueCountdown.name, VueCountdown);

export default {
  name: "header-nav",
  data() {
    return {
      windowHeight: 0,
      dividendPercentage: 90,
      dividendsModal: false,
      dividendPool: 0,
      currentMiningValue: 0,
      nextMiningValue: 0,
      platformStage: 0,
      daysinarow: 0,
      platformLevel: 0,
      checkinReward: 0,
      storeGST: 0,
      storeGSTText: "",
      checkinPoint: 0,
      frozenGST: 0,
      checkinPool: 0,
      lotteryPool: 0,
      distributionPool: 0,
      availableGST: 0,
      countDownInSecond: 0,
      availableGSTText: "",
      freezeGST: 0,
      unfreezeGST: 0,
      unfreezeGSTText: "",
      tronAddressNHex: "ABCD",
      dropDapp: false,
      ifNotAuth: false,
      receiveAmount: 0,
      perValue: [],
      isLogin: false,
      referralModal: false,
      checkinModal: false,
      lotteryModal: false,
      referralLink: "Link Here",
      copySucceeded: null,
      refTag: false,
      regTagTitle: "Copy this link"
    };
  },

  computed: {
    ...mapState([
      "tronWeb",
      "storeContractAddress",
      "storeTokenAddress",
      "tronLoggedIn",
      "tronAddress",
      "checkin",
      "lottery"
    ]),
    checkTronLogin() {
      return this.tronLoggedIn;
    },
    checkCheckIn() {
      return this.checkin;
    },
    checkLottery() {
      return this.lottery;
    },
    checkTronAddress() {
      return this.tronAddress;
    },
    backgroundStyle: function() {
      return {
        width: this.dividendPercentage + "%"
      };
    }
  },
  watch: {
    async checkTronLogin(value) {
     console.log('tron address', this.tronWeb.trx);
      if (value) {
        this.isLogin = true;

        console.log("headernav : check tron login is called");

        const nonHex = this.tronWeb.address.fromHex(
          (await this.tronWeb.trx.getAccount()).address.toString()
        );

        this.tronAddressNHex = nonHex;

         console.log('nonhex',nonHex);
      }
    },
    checkCheckIn(value) {
      console.log("Header : ", value);
      if(!value.myDaysInARow == undefined)
      {
        this.daysinarow = value.myDaysInARow;
      }
      const daysinarow = value.myDaysInARow - 1;
      this.checkinPoint = 20 + daysinarow * daysinarow * 5;
    },
    checkLottery(value)
    {
      this.countDownInSecond = value.countDownInSecond;
    },
    async checkTronAddress(value) {
      console.log("headernav : Check tron address is called" + value);

      if (!value == "none") {
        this.isLogin = true;    

        console.log("Tron is logged inABCD");

        const nonHex = this.tronWeb.address.fromHex(value);

        console.log("New address is this one: " + nonHex);

        this.tronAddressNHex = nonHex;

       
      }
    }
  },
  async mounted() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
    await this.$store.dispatch("getTronWebData");

    await this.$store.dispatch("getLogin");

    await this.$store.dispatch("getTronAddress");

    this.$store.dispatch("getCheckinData", {
      tronaddress: this.tronAddressNHex
    });

    this.$store.dispatch("getLotteryData", {
      tronaddress: this.tronAddressNHex
    });

    var addressCheck = "";

    if (await this.tronWeb.trx) {
      console.log("My bets: " + (await this.tronWeb.trx.getAccount()).address);
      addressCheck = (await this.tronWeb.trx.getAccount()).address;
    } else {
      console.log("My bets: " + "False");
    }

    if (!addressCheck == "") {
      this.isLogin = true;

      console.log("Tron is logged in");

      const nonHex = this.tronWeb.address.fromHex(
        (await this.tronWeb.trx.getAccount()).address.toString()
      );

      this.tronAddressNHex = nonHex;

       this.referralLink = "gamestation.io/referral/" + this.tronAddressNHex;
       
    } else {
      this.isLogin = false;
    }

    this.getData();
  },
  methods: {
    async getData() {
      this.$store.dispatch("getContractAddress");

      console.log("Store contract address : " + this.storeContractAddress);

      const contractAddress = this.storeContractAddress;

      console.log("Contract in use : " + contractAddress);

      window.contract = await window.tronWeb.contract().at(contractAddress);

      const balance = await window.contract
        .getUserBalance(this.tronAddressNHex)
        .call();

      const gstBalance = balance.balance.toNumber();

      const storeBalance = await window.contract
        .getUserStoreBalance(this.tronAddressNHex)
        .call();

      this.storeGST = storeBalance.balance.toNumber() / 100000000;

      this.storeGSTText = this.storeGST + " GST";

      console.log("STORE GST OF USER: ", this.storeGST);

      this.availableGST = gstBalance / 100000000;

      this.availableGSTText = this.availableGST + " GST";

      console.log("GST BALANCE OF USER: ", gstBalance);

      this.frozenGST =
        (await window.contract.totalFrozen().call()).toNumber() / 100000000;

      this.platformLevel = await window.contract.platformLevel().call();

      this.platformStage = await window.contract.platformStage().call();

      this.currentMiningValue =
        1000 + 20 * (this.platformStage - 1) + 200 * (this.platformLevel - 1);

      this.nextMiningValue =
        1000 + 20 * (this.platformStage - 1) + 200 * this.platformLevel;

      const userFrozenBalance = await window.contract
        .getUserFrozenBalance(this.tronAddressNHex)
        .call();

      const frozenUSERGST = userFrozenBalance.balance.toNumber();

      this.unfreezeGST = frozenUSERGST / 100000000;

      this.unfreezeGSTText = this.unfreezeGST + " GST";

      console.log("NavHeader: Frozen Balance: ", frozenUSERGST);

      console.log(
        "NavHeader: Contract balance:",
        ((await window.contract.contractBalance().call()) / 1000000).toString()
      );

      const contractBalance = ((await window.contract.contractBalance().call()) / 1000000).toString();

      this.checkinPool = (1 * (contractBalance-20000)).toFixed(2);

      this.lotteryPool = (1 * (contractAddress-20000)).toFixed(2);

      const totalDaysInARow = this.checkin.totalDaysInARow - 1;

      const totalPoints = 20 + totalDaysInARow * totalDaysInARow * 5;

      this.checkinReward = (
        (this.checkinPoint / totalPoints) *
        this.checkinPool
      ).toFixed(2);

      this.dividendPool = (0.7 * (contractBalance - 12000)).toFixed(2);

      this.distributionPool = this.dividendPool / 2;

      const TRXperGST = this.distributionPool / this.frozenUSERGST;

      const receiveTRX = TRXperGST * this.freezeGST;

      this.receiveAmount = receiveTRX;
    },

    onlyNumber($event) {
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;
      if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) {
        $event.preventDefault();
        this.bettingAmount = 0;
      }
    },
    transferToken() {
      window.contract
        .transferDirect(this.tronAddressNHex, this.storeGST * 1000000)
        .send({
          shouldPollResponse: true,
          callValue: 0
        })
        .then(res => {
          console.log("GST transferred from store" + res);
          this.flash(
            "" +
              this.availableGST +
              " GST has been transferred to your account!",
            "info",
            {
              timeout: 2000
            }
          );
          this.getData();
        })
        .catch(err => window.alert(err));
    },
    freezeBalance() {
      if (this.availableGST < 2) {
        this.flash("Minimum of 2 GST is required to freeze.", "error", {
          timeout: 2000
        });
      } else {
        window.contract
          .freeze(this.availableGST * 1000000)
          .send({
            shouldPollResponse: true,
            callValue: 0
          })
          .then(res => {
            console.log("GST Frozen" + res);
            this.flash(
              "" +
                this.availableGST +
                " GST has been frozen from your account!",
              "info",
              {
                timeout: 2000
              }
            );
            this.getData();
          })
          .catch(err => window.alert(err));
      }
    },
    unFreezeBalance() {
      window.contract
        .unfreeze(this.availableGST * 1000000)
        .send({
          shouldPollResponse: true,
          callValue: 0
        })
        .then(res => {
          console.log("GST UnFrozen" + res);
          this.flash(
            "" +
              this.availableGST +
              " GST has been unfrozen from your account!",
            "info",
            {
              timeout: 2000
            }
          );
          this.getData();
        })
        .catch(err => window.alert(err));
    },
    loginNow() {
      this.$store.dispatch("loginPopUpStatus", {
        isLogin: true
      });
    },
    showSideNav() {
      this.$root.chatBoxShow = false;
      if (this.$root.sideNav == false) {
        this.$root.sideNav = true;
      } else {
        this.$root.sideNav = false;
      }
    },
    handleResize() {
      this.windowHeight = window.innerHeight;
      this.windowWidth = window.innerWidth;
      if (this.windowWidth > 991) {
        this.$root.chatBoxShow = true;
      } else {
        this.$root.chatBoxShow = false;
      }
    },
    dropDownNav(name) {
      if (name == "dapps") {
        if (this.dropDapp == false) {
          this.dropDapp = true;
        } else {
          this.dropDapp = false;
        }
      }
    },
    onCopyReferralLink: function(e) {
      console.log(e);
      this.regTagTitle = "Copied";
    },
    onErrorReferralLink: function(e) {
      console.log(e);
      this.regTagTitle = "Failed to copy texts";
    },
    onShowRefTitle: function() {
      this.regTagTitle = "Copy this link";
      this.refTag = true;
    }
  }
};
</script>

